        aeGenerateAccount: async function () {
            let mnemonic = bip39.generateMnemonic();

            const seed = await bip39.mnemonicToSeed(mnemonic);
            const wallet = getHdWalletAccountFromSeed(seed, 0);
            this.responseSuccess("aeGenerateAccount", {
                mnemonic: mnemonic,
                publicKey: wallet.publicKey,
                secretKey: wallet.secretKey,
            });
        },

        aeRestoreAccountMnemonic: async function (mnemonic) {
            if (!bip39.validateMnemonic(mnemonic)) {
                this.responseError(
                    "aeRestoreAccountMnemonic",
                    "mnemonic validate error"
                );
                return;
            }

            const seed = await bip39.mnemonicToSeed(mnemonic);
            const wallet = getHdWalletAccountFromSeed(seed, 0);
            this.responseSuccess("aeRestoreAccountMnemonic", {
                mnemonic: mnemonic,
                publicKey: wallet.publicKey,
                secretKey: wallet.secretKey,
            });
        },

